<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container">
      <div class="login">
        <div class="row">
          <div class="col-xl-5">
            <div class="log_img">
              <div class="log_img_title">{{$t('login')}}</div>
              <img src="../../../assets/images/Grouppp.png" alt="">
            </div>
          </div>
          <div class="col-xl-7">
            <div class="log_form">
             <div>
               <form @submit.prevent="login()">
                 <label>{{$t('email')}} *</label>
                 <div class="input_text">
                   <input type="text" v-model="username" placeholder="Email">
                 </div>
                 <label>{{$t('password')}}*</label>
                 <div class="input_text">
                   <input  :type="passwordFieldType"
                           id="password"
                           v-model="password" :placeholder="$t('password')">
                   <a @click="togglePasswordVisibility" class="password_show" href="#">
                     <b-icon icon="eye" aria-hidden="true"></b-icon>
                   </a>
                 </div>
                 <div class="forgot_password">
                   <router-link to="/" class="decoration">
                     {{$t('forget_password')}}
                   </router-link>
                 </div>
                 <div class="log_btn">
                   <button> {{$t('login')}}</button>
                 </div>
                 <div class="phone_link">
                   <router-link to="/phone_log" >
                     <div>
                       Telefon orqali kirish
                     </div>
                   </router-link>
                 </div>
               </form>
               <div class="reg_links">
                 <router-link to="/registration">
                   <div>{{$t('reg_quiz')}}? <span>{{$t('reg')}}</span></div>
                 </router-link>
               </div>

             </div>
            </div>
          </div>
        </div>

      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
export default {
  components: {Footer, Header},
  data(){
    return{
      email:'',
      password:'',
      username:'',
      passwordVisible: false
    }
  },
  computed: {
    passwordFieldType() {
      return this.passwordVisible ? 'text' : 'password';
    }
  },

  methods:{
    togglePasswordVisibility() {
      this.passwordVisible = !this.passwordVisible;
    },
    login(){
      this.$axiosDefault.post('/students/login/',{
        "email":this.username,
        "password":this.password
      })
          .then(res=>{
            localStorage.setItem('reg_token',res.data.access)
            this.$store.dispatch('getMe')
            this.$router.push('/')

            })
          .catch(()=>{
            this.$toast("Email yoki parol xato ", {
              timeout: 2000,
              type:"error"
            });
          })

    }
  }
}
</script>
